.user_list {
    width: 1380px;
    height: 375px;
    padding: 25px;
    margin-top: 20px;
    margin-bottom: 20px;
    background-color: #FFFFFF;
    box-shadow: 1px 1px 20px -5px rgba(0, 0, 0, 0.25);
    border-radius: 20px;

}

.user__top {
    display: grid;
    grid-template-columns: 48px 160px 160px 160px 220px 136px 160px 155px 48px;
    margin-bottom: 25px;
}

span {
    background-color: #FFFFFF;
    box-shadow: inset 1px 1px 0px #E5E9EB, inset -1px -1px 0px #E5E9EB;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 48px;

    font-weight: 600;
    font-size: 14px;
    line-height: 24px;

    letter-spacing: -0.006em;

    color: #1A2024;
}

.user_infos {
    display: grid;
    grid-template-columns: 48px 160px 160px 160px 220px 136px 160px 155px 48px;
}