.job {
    width: 373px;
    height: 375px;
    background-color: #FFFFFF;
    box-shadow: 1px 1px 20px -5px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    padding: 23px 26px;
}

.job h2 {
    font-weight: 400;
    font-size: 28px;
    line-height: 34px;
    color: #000000;

}

.job hr {
    border: 1px solid #CCCCCC;
    width: 100%;
    margin-bottom: 50px;
}

.job__label, .job__type {
    width: 251px;
    height: 48px;
    display: grid;
    grid-template-columns: 20% 60% 20%;
    margin: 0 auto;
    margin-bottom: 23px;


}

.job__type {
    width: 251px;
    height: 48px;
    display: grid;
    grid-template-columns: 20% 60% 20%;
    margin: 0 auto;
}

.job__label span {
    height: 48px;
    background-color: #FFFFFF;
    box-shadow: inset 1px 1px 0px #E5E9EB, inset -1px -1px 0px #E5E9EB;
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: -0.006em;
    color: #1A2024;
    display: flex;
    align-items: center;
    justify-content: center;
}

.job__type span {
    height: 48px;
    background-color: #FFFFFF;
    box-shadow: inset 1px 1px 0px #E5E9EB, inset -1px -1px 0px #E5E9EB;
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: -0.006em;
    color: #1A2024;
    display: flex;
    align-items: center;
    justify-content: center;
}

.job__delete_btn {
    cursor: pointer;
    
}