.job__form {
    width: 600px;
    height: 300px;
    background-color: #FFFFFF;
    box-shadow: -3px -6px 20px -5px rgba(0, 0, 0, 0.25), 3px 6px 20px -5px rgba(0, 0, 0, 0.25);
    border-radius: 20px;

    padding: 23px 25px;

    position: absolute;
    top: 385px;
    left: 764px;
}

.job__form h2 {
    font-weight: 400;
    font-size: 28px;
    line-height: 34px;
    color: #000000;
}

.job__form hr {
    border: 1px solid #CCCCCC;
    margin-bottom: 30px;
}

.job__form label {
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    display: flex;
    align-items: center;
    color: #303940;
    margin-bottom: 5px;
}

.job__input {
    width: 100%;
    height: 40px;
    background-color: #FFFFFF;
    border: 1px solid #E5E9EB;
    box-sizing: border-box;
    border-radius: 6px;

    padding: 0 15px;
    margin-bottom: 56px;
}

.reset {
    width: 160px;
    height: 40px;
    background-color: #FFFFFF;
    border: 1px solid #E5E9EB;
    box-sizing: border-box;
    border-radius: 6px;

    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    letter-spacing: -0.006em;
    color: #0067F4;

    cursor: pointer;
}

.save {
    width: 160px;
    height: 40px;
    left: 1198px;
    top: 630px;

    background-color: #0067F4;
    border-radius: 6px;
    padding: 8px 16px;
    border: none;
    margin-left: 15px ;


    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    letter-spacing: -0.006em;
    color: #FFFFFF;

    cursor: pointer;
}
.save:active {
    color: #0067F4;
    background-color: #FFFFFF;
    transition: 200ms;
    border:1px solid #0067F4;
}

.reset:active {
    border: 1px solid #6b757583;
    transition: 200ms;


}

.controllers {
    width: 340px;
    display: flex;
    margin-left: auto;
}