.user_info {
    width: 1380px;
    height: 800px;
    display: flex;
    justify-content: space-between;
    background-color: #FFFFFF;
    box-shadow: 3px 6px 20px -5px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
    padding: 25px;
}

hr {
    border: 1px solid #CCCCCC;
    width: 100%;
    margin-bottom: 35px;
}

.main {
    width: 650px;
    height: 650px;
    padding: 25px;
    display: flex;
    flex-direction: column;

    background-color: #FFFFFF;
    box-shadow: 3px 6px 20px -5px rgba(0, 0, 0, 0.25), -3px -6px 20px -5px rgba(0, 0, 0, 0.25);
    border-radius: 20px;
}

label {
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;
    margin-bottom: 5px;


    color: #303940;

}

.main input {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 8px 16px;

    width: 600px;
    height: 40px;

    background-color: #FFFFFF;

    border: 1px solid #E5E9EB;
    box-sizing: border-box;
    border-radius: 6px;
    margin-bottom: 15px;
}

.work {
    width: 600px;
    height: 300px;
    padding: 25px;

    background-color: #FFFFFF;
    box-shadow: -3px -6px 20px -5px rgba(0, 0, 0, 0.25), 3px 6px 20px -5px rgba(0, 0, 0, 0.25);
    border-radius: 20px;

    display: flex;
    flex-direction: column;
}

.work input {
    padding: 8px 16px;

    width: 100%;
    height: 40px;
    left: 760px;
    top: 189px;


    background-color: #FFFFFF;

    border: 1px solid #E5E9EB;
    box-sizing: border-box;
    border-radius: 6px;
    margin-bottom: 15px;
}

.job_exp {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.select {
    padding: 8px 8px 8px 16px;

    width: 270px;
    height: 40px;
    left: 770px;
    top: 270px;


    background-color: #FFFFFF;

    border: 1px solid #E5E9EB;
    box-sizing: border-box;
    border-radius: 6px;
}
.user_controllers .reset {
    width: 160px;
    height: 40px;
    background-color: #FFFFFF;
    border: 1px solid #E5E9EB;
    box-sizing: border-box;
    border-radius: 6px;

    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    letter-spacing: -0.006em;
    color: #0067F4;

    cursor: pointer;
}

.user_controllers .save {
    width: 160px;
    height: 40px;
    left: 1198px;
    top: 630px;

    background-color: #0067F4;
    border-radius: 6px;
    padding: 8px 16px;
    border: none;
    margin-left: 15px ;


    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    letter-spacing: -0.006em;
    color: #FFFFFF;

    cursor: pointer;
}
.user_controllers .save:active {
    color: #0067F4;
    background-color: #FFFFFF;
    transition: 200ms;
    border:1px solid #0067F4;
}

.user_controllers .reset:active {
    border: 1px solid #6b757583;
    transition: 200ms;


}

.user_controllers {
    width: 340px;
    display: flex;
    position: absolute;
    top: 780px;
    left: 1000px;
}